import * as React from "react"
import {Grid} from "@mui/material";
import Typography from "@mui/material/Typography";

import {FC, useEffect, useState} from "react";
import {Link} from "gatsby";
import { BrowserView, MobileView } from "react-device-detect";

const linkStyle = {
    color: `black`,
    textDecoration: `none`,
    padding: `0px 7px`,
}

type ProjectProps = {
    name: string,
    date: string,
    body: string,
    image?: string,
    url: string
}

export const MobileProject: FC<ProjectProps> = ({
    name,
    date,
    body,
    image,
    url
}) => {

    return (
        <Grid container spacing={2} style={{height: "40%"}}>
            <Grid item>
                <Link to={url} style={linkStyle}>
                    <Typography variant="h5">
                        {name}
                    </Typography>
                </Link>
                <Typography variant="body2" color={"gray"}>
                    {date}
                </Typography>
            </Grid>
            <Grid item justifyContent={"center"}>
                {
                    image &&
                    <img src={image} alt={"project_image"} style={{maxWidth:"100%"}}/>
                }
            </Grid>
            <Grid item>
                <Typography variant="body1">
                    {body}
                </Typography>

            </Grid>
        </Grid>
    )
}

export const BrowserProject: FC<ProjectProps> = ({
    name,
    date,
    body,
    image,
    url
}) => {

    return (
        <Grid container spacing={2} style={{height: "40%"}} justifyContent={"center"}>
            <Grid item xs={6}>
                <Link to={url} style={linkStyle}>
                    <Typography variant="h5">
                        {name}
                    </Typography>
                </Link>
                <Typography variant="body2" color={"gray"}>
                    {date}
                </Typography>
                <Typography variant="body1">
                    {body}
                </Typography>
            </Grid>
            <Grid item xs={6} justifyContent={"center"}>
                {
                    image &&
                    <img src={image} alt={"project_image"} style={{maxWidth:"100%"}}/>
                }
            </Grid>
        </Grid>
    )
}


export const Project: FC<ProjectProps> = ({
    name,
    date,
    body,
    image,
    url
}) => {

    if (typeof window === `undefined`) {
        return(<></>);
    }

    const mobileScreenWidth = 900
    const [isMobile, setIsMobile] = useState(window.innerWidth < mobileScreenWidth)

    useEffect(() => {
        function handleWindowResize() {
            setIsMobile(window.innerWidth < mobileScreenWidth)
        }
        window.addEventListener('resize', handleWindowResize);
        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, []);
    return (
        <>
            {
                isMobile ?
                    <MobileProject
                        name={name}
                        date={date}
                        body={body}
                        url={url}
                        image={image}
                    />
                    :
                    <BrowserProject
                        name={name}
                        date={date}
                        body={body}
                        url={url}
                        image={image}
                    />
            }
        </>
    )
}
